import React, { Component } from 'react';
import { Input, Form, Button } from 'antd';

@Form.create({ name: 'FormSelectPlan' })
export default class FormSelectPlan extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
      const { handleSelectPlan } = this.props;
      handleSelectPlan(values.planCode);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSubmit} className="login-form">
        {' '}
        <Form.Item label="Nội dung sự kiện" hasFeedback>
          {getFieldDecorator('planCode', {
            initialValue: '',
            rules: [
              {
                required: true,
                message: 'Vui lòng nhập thông tin!',
              },
            ],
          })(<Input />)}
        </Form.Item>
        <div className="" style={{ textAlign: 'right' }}>
          <Button onClick={this.props.onCancel}>Cancel</Button>
          <Button onClick={this.handleSubmit} type="primary">
            OK
          </Button>
        </div>
      </Form>
    );
  }
}



